<template>
  <div class="photo-item">
    <img :src="photo" class="img" ref="img" />

    <div class="footer">
      <div class="cancel" @click="concel"></div>
      <div class="confirm" @click="confirm"></div>
    </div>
  </div>
</template>

<script>
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import { updateUserAvatar } from '@/api/user.js'
export default {
  name: 'EditPhotoIndex',
  props: {
    photo: {
      type: [String, Object],
      required: true
    }
  },
  data () {
    return {
      cropper: null
    }
  },
  methods: {
    concel () {
      this.$emit('close')
    },
    confirm () {
      this.cropper.getCroppedCanvas().toBlob(blob => {
        this.updateAvatar(blob)
      })
    },
    async updateAvatar (blob) {
      this.$toast.loading({
        message: '...',
        forbidClick: true,
        duration: 0
      })
      const formData = new FormData()
      formData.append('photo', blob)
      const res = await updateUserAvatar(formData)
      // 
      if (!res) {
        this.$toast.fail('')
      } else if (res.message === 'OK') {
        this.$emit('update-avatar', res.data.photo)
        this.$emit('close')
        this.$toast.success('')
      }
    }
  },
  mounted () {
    const image = this.$refs.img
    this.cropper = new Cropper(image, {
      viewMode: 1, // 
      dragMode: 'move', // 
      aspectRatio: 1, // 
      // autoCropArea: 1, // 
      cropBoxMovable: false, // 
      cropBoxResizable: false, // 
      background: false // 
    })
  }
}
</script>

<style lang="scss" scoped>
.photo-item {
  background: #000;
  height: 100%;
  .footer {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    .cancel,
    .confirm {
      width: 90px;
      height: 90px;
      font-size: 28px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
    }
  }
}
.img {
  display: block;
  max-width: 100%;
}
</style>
