<template>
  <div class="gender-item">
    <van-picker
      title=""
      show-toolbar
      :columns="columns"
      @confirm="onConfirm"
      @cancel="onCancel"
      item-height="30px"
      :default-index="userProfile.gender"
    />
  </div>
</template>

<script>
import { editProfile } from '@/api/user'
export default {
  name: 'EditGenderIndex',
  props: {
    userProfile: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      columns: ['', '']
    }
  },
  methods: {
    // 
    async onConfirm (value, index) {
      this.$toast.loading({
        message: '...',
        forbidClick: true, // 
        duration: 0 // 
      })
      const res = await editProfile({
        gender: index
      })
      // console.log(res)
      if (res.message === 'OK') {
        this.$toast.success('')
      } else {
        this.$toast.fail('')
      }
      this.$emit('close', res.data)
    },
    //  
    onCancel () {
      this.$emit('close')
    }
  }

}
</script>

